.profile__form_button {
    margin: 0 auto 0;
    background-color: #202020;
    border: 0;
    font-weight: normal;
    font-size: 13px;
    line-height: 16px;
    color: #FFFFFF;
    cursor: pointer;
}

.profile__form_button-disabled {
    margin: 0 auto 0;
    background-color: #202020;
    border: 0;
    font-weight: normal;
    font-size: 13px;
    line-height: 16px;
    color: gray;
    cursor: pointer;
}

.profile__form_button:hover {
    opacity: 0.8;
}